body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:source sans pro, sans-serif;font-size:1rem;line-height:1.5}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.container{display:flex;justify-content:space-between;align-items:center}.container--col{display:flex;flex-direction:column;align-items:center}.button{text-decoration:none;font-size:14px;padding:7px 10px;border:1px solid beige;border-radius:5px;background-color:transparent}.nav{padding:20px;background-color:#ffd86c}.logo{text-decoration:none;font-size:25px;line-height:1.5;font-weight:800;text-shadow:2px 1.5px 5px black;color:#ffc017;transition:text-shadow 0.3s ease-in-out}.logo:hover{text-shadow:2px 1.5px 11px 10px black}.list{display:flex;flex-wrap:wrap;align-items:center}.list:last-child{margin-left:0}.list{list-style:none;margin:0;padding:0}.item{list-style:none;margin:0;padding:0;margin-left:5px}.link{display:flex;align-items:center;text-decoration:none;color:rgba(0,0,0,0.5);margin-right:2px}.link:hover{color:#242222}.link:active{color:rgba(0,0,0,0.5)}.active{color:#1a8917}.icon{width:20px;height:20px}.avatar{position:relative;width:40px;height:40px;display:flex;justify-content:center;align-items:center;flex-shrink:0;overflow:hidden;font-size:1.25rem;font-family:"Roboto", "Helvetica", "Arial", sans-serif;line-height:1;border-radius:50%;margin-right:5px}.image{color:transparent;width:100%;height:100%;object-fit:cover;text-align:center;text-indent:10%}
