body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:source sans pro, sans-serif;font-size:1rem;line-height:1.5}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.container{display:flex;justify-content:space-between;align-items:center}.container--col{display:flex;flex-direction:column;align-items:center}.button{text-decoration:none;font-size:14px;padding:7px 10px;border:1px solid beige;border-radius:5px;background-color:transparent}.auth-page{background-color:#1a8917}.title{font-size:35px;line-height:1;padding:0;margin:0;margin-top:10px;color:#242222}.questionLink{text-decoration:none;color:#1a8917}.questionLink:hover{text-decoration:underline}.field{display:flex;flex-direction:column;justify-content:flex-end;align-items:flex-end;list-style:none;border:0;margin:0;padding:0}.inputText{font:inherit;border:1px solid #f1e895;text-decoration:none;padding:7px 5px;margin-bottom:7px;border-radius:5px}.button{background-color:#ffc017}.button:hover{background-color:#cc980a}.button:active{color:#242222;opacity:70%}
